.App {
  width: 100vw;
  height: 100vh;
  user-select: none;
}

.selected-dates {
  height: 22px;
  /* background-color: rgb(231, 231, 231); */
  background-color: rgb(244, 246, 248);
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
  margin-top: -22px;
  box-shadow: 0 3px 6px #ddd;
  display: flex;
}

.dates-display.hebrew {
  flex-direction: "row-reverse";
  margin-right: 5%;
}

.dates-display {
  margin-left: 5%;
  font-size: 1em;
  font-weight: 500;
}

.date-range-picker {
  position: absolute;
  width: 795px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  top: 30%;
  left: 10%;
}

.calendar-component {
  text-align: center;
  position: absolute;
  height: 295px;
  width: 255px;
  border-bottom-left-radius: 12px;
  border-bottom-right-radius: 12px;
  box-shadow: 0 3px 6px #ddd;
  padding: 0px 5px 10px 5px;
  background-color: rgb(244, 246, 248);
}

.mute-icon {
  float: right;
  cursor: pointer;
  margin-right: 2em;
  width: 1.5rem;
  height: 1.5rem;
}

.picker-toggler,
.hand-right {
  float: left;
  margin-left: 2em;
}

.picker-toggler {
  width: 1.5rem;
  height: 1.5rem;

  border-radius: 50%;
  border: 0;
  cursor: pointer;
}

.hand-right {
  margin-right: 5px;
  height: 30px;
}

.color-picker {
  float: left;
  height: 1.5rem;
}

.circle-picker {
  padding-top: 10px !important;
  margin-right: 0 !important;
}
